import React, { useContext } from "react";
import axios from 'axios';
// reactstrap components
import {

  Card,
  CardHeader,
  Container,
  Row,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Col,
  Spinner,
  CardFooter,
  Button
} from "reactstrap";
// core components
import Header from "components/Headers/Header.js";
import Userlisttable from "./tables/Userlisttable";
import Paginationtable from "./tables/Paginationtable";
import { ToastContainer, toast } from 'react-toastify';
import { UsersContext, QueryContext, PaginationContext } from './contexts/UserContext';

const Userslist = () => {
  const [users, setUsers] = useContext(UsersContext);
  const [q, setQ] = useContext(QueryContext);
  const {currentpage, setCurrentpage, totalpages,setTotalpages} = useContext(PaginationContext); 
  

  const paginate = () => {   
    setCurrentpage(currentpage+1)
    fetchusers()
    console.log(currentpage)
    
  }
  const fetchusers = async () => {
    const url="https://app.kiranvoleti.com/getuserlist";
    let form_data = new FormData();
    form_data.append('page', currentpage);
    const config = {
      headers: {
          'content-type': 'application/json',
          // 'X-CSRFToken': this.getCookie('csrftoken')
      }
    }
    await axios.post(url, form_data, config)
    .then(res=>{            
        setUsers(res.data.users);                        
        
    })
    .catch(err=>{
        console.log(err)
    })
}



const deleteUser = async (e) => {        
  // Deleter



   let form_data = new FormData();
   var list = []
   var allelm = document.querySelectorAll("input[name=childbox]");

   await allelm.forEach(elm => {
       if (elm.checked) {
           list.push(elm.id)           
       }
   })
  

   form_data.append('deletelist', list);
   form_data.append('tablename', 'auth_user');
   let url = 'https://app.kiranvoleti.com/bulkdeltereactusers';
   // let url = 'https://jsonplaceholder.typicode.com/todos';
   const config = {
       headers: {
           'content-type': 'application/json',
           'X-CSRFToken': getCookie('csrftoken')
       }
   }

   axios.post(url, form_data, config)
       .then(res => {

           tosttrigger(res.data.msg, "success");
           fetchusers()

       })
       .catch(err =>
           tosttrigger("Something Went Wrong !", "error")

       )
}
const deleteUserind = async (e) => {        
  // Deleter



   let form_data = new FormData();
   var list = []
   list.push(e.target.dataset.id)      
 
  

   form_data.append('deletelist', list);
   form_data.append('tablename', 'auth_user');
   let url = 'https://app.kiranvoleti.com/bulkdeltereactusers';
   // let url = 'https://jsonplaceholder.typicode.com/todos';
   const config = {
       headers: {
           'content-type': 'application/json',
           'X-CSRFToken': getCookie('csrftoken')
       }
   }

   axios.post(url, form_data, config)
       .then(res => {

           tosttrigger(res.data.msg, "success");
           fetchusers()

       })
       .catch(err =>
           tosttrigger("Something Went Wrong !", "error")

       )
}
function tosttrigger(msg,status){
  if(status === "success"){

      toast.success(msg, {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
      });

      }else{
          toast.error(msg, {
              position: "top-right",
              autoClose: 3000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
          });

}
  
}

const getCookie = (name) => {
  let cookieValue = null;
  if (document.cookie && document.cookie !== '') {
    const cookies = document.cookie.split(';');
    for (let i = 0; i < cookies.length; i++) {
      const cookie = cookies[i].trim();
      // Does this cookie string begin with the name we want?
      if (cookie.substring(0, name.length + 1) === (name + '=')) {
        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
        break;
      }
    }
  }
  return cookieValue;
}


 
  
  
  return (
    <>
      <Header />
      {/* Page content */}
      <Container className="mt--7" fluid>
        {/* Table */}
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <h3 className="mb-0">Users</h3>
              </CardHeader>
              <Form>
                <Row>
                  <Col md="4 m-2">
                    <FormGroup>
                    <InputGroup className="mb-4">
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="ni ni-zoom-split-in" />
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input value= {q} onChange={e => setQ((e.target.value).toLowerCase())} placeholder="Filter" type="text" />
                </InputGroup>
                    </FormGroup>
                  </Col>
                  <Col md="4 m-2">
                    <FormGroup>
                    <InputGroup className="mb-4">
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="ni ni-zoom-split-in" />
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input placeholder="Search from Database" type="text" />
                </InputGroup>
                    </FormGroup>
                  </Col>                  
                </Row>
              </Form>              

              <Userlisttable deleteUserind = {deleteUserind} deleteUser = {deleteUser} users={users} totalpages={totalpages} />
              <Button color="primary" size="sm" onClick={paginate}>Next</Button>

              {users.length === 0 ? <Spinner color="primary" />

              :


             null
              
              
              }
              <CardFooter className="py-4">
                                    
                      
                      
                </CardFooter>
              
            </Card>
          </div>
        </Row>
      </Container>
    </>
  );
}


export default Userslist;
